* {
    margin: 0;
    padding: 0;
}
nav{
    background-color:rgb(109, 119, 119);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
}
nav h1{
    color: gold;
    font-size: 30px;
    padding-right: 10px;
    padding-top: 15px;
}
nav h1:hover{
    color: red;
}
nav ul{
    display: flex;
    justify-content: space-between;
    width: 80%;
    align-items: center;
}
nav ul a{
    font-size: 22px;
    font-weight: 300;
    color:whitesmoke;
    text-decoration: none;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
nav ul a:hover{
    color: lime;
}