*{
    margin: 0;
    padding: 0;
}
.home{
    width: 100%;
    margin: 0 auto;
    margin-top: 90px;
    height: 100%;
}
.home__container{
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.home__title{
    color:rgb(235, 8, 148);
}
.home__title:hover{
color: limegreen;
}
.home__formContainer{
    box-shadow: 1px 4px 12px -2px rgba(0,0,0,4.3);
    background:url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRIrftS6YqgBMt7Zi5uqXn-I7xknb5kwZrYUw&s);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 30px;
    border-radius: 10px;
    width: 500px;
    
    
}
.home__formContainer input{
    border: 1px solid #e5e5e5;
    outline: none;
    padding: 15px 10px;
    width: 80%;
    border-radius:8 px;
    margin: 10px 0;
    background: transparent;
    border: 1px solid gray;
    color:wheat;
    font-size: 16px;

}

.home__formContainer button{
background:black;
border: 1px solid white;
outline: none;
font-size: 18px;
color:white;
padding: 10px;
width: 200px;
margin-top: 30px;
border-radius: 12px;
cursor: pointer;
}
.home__formContainer input::placeholder{
    color:lightslategray;
}












